.about {
    margin: 0 auto;
    padding: 102px 0 33px 0;
    
}

.about__main{
    background-color: var(--color-korich);
}

.about-container {

}

.about-description {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.about-description__heading {
    margin: 0;
    margin-right: -10px;
    font-family: 'SantaCatarina';
    color: var(--color-space);
    font-size: 90px;
    font-weight: 400;
    line-height: 90.81px;
    text-transform: uppercase;
}

.about-description-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 39px
}

.about-description-container__image {
    width: 822px;
    height: 411.46px;
    border-top-left-radius: 500px;
    border-top-right-radius: 500px;
}

.about-description-container__contain {
    max-width: 568px;
}

.about-description-container__text {
    font-family: 'GilroyRegular';
    margin: 0;
    color: var(--color-space);
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

.about-description-container__text--margin-top {
    margin-top: 60px;
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.list__item {
    padding-bottom: 15px;
}

.list__num {
    font-family: 'GilroyRegular';
    font-weight: 700;
    width: 15px;
    font-size: 25px;
    line-height: 35px;
    color: var(--color-bej);
}


.about-description-two {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.about-description-two__swap-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: -104px;
    padding-top: 218px;
}

.about-description-two__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 50px;
}

.container-one {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
}

.container-two {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 31px;
}

.about-description-two__text {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    z-index: 2;
    gap: 31px;
    margin: 0;
    font-family: 'GilroyRegular';
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: var(--color-space);
    max-width: 636px;
    text-transform: uppercase;
}

.about-description-two__text--heading {
    font-family: 'GilroyRegular';
    position: relative;
    z-index: 2;
    font-size: 30px;
    line-height: 42px;
}

.about-description-two__image-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.about-description-two__image-girl {
    position: relative;
    z-index: 1;
    top: 70px;
    left: -75px;
    border-bottom-left-radius: 470px;
    border-top-left-radius: 470px;
    height: 721px;
    width: 361px;
}

.about-description-two__image-lenta {
    position: relative;
    z-index: 0;
    width: 493px;
    height: 528px;
    margin-top: -163px;
    margin-left: -309px;
}

.about-description-two__citata {
    margin: 0;
    bottom: 75px;
    font-style: normal;
    position: relative;
    z-index: 1;
    font-family: 'SantaCatarina';
    font-weight: 400;
    font-size: 129px;
    line-height: 130.16px;
    color: var(--color-space);
    max-width: 1110px;
    text-transform: uppercase;
}


@media (max-width: 1522px) {
    .about {
        padding-bottom: 0;
    }

    .about-description-container__image {
        width: 752px;
        height: 377px;
    }

    .about-description-two__swap-container {
        display: flex;
        flex-direction: column-reverse;
        justify-content: flex-start;
        align-items: flex-end;
        margin-top: -668px;
    }

    .about-description-two__image-container {
        flex-direction: column-reverse;
        align-items: flex-end;
    }

    .about-description-two__image-girl {
        height: 678px;
        width: 340px;
        top: 0;
        left: 0;
    }
    
    .about-description-two__image-lenta {
        width: 493px;
        height: 528px;
        opacity: 0.3;
        margin-top: 0;
        margin-left: 0;
        top: 22px;
        left: 20px;
    }

    .about-description-two__container {
        align-self: flex-start;
    }

    .about-description-two__text {
        font-size: 13px;
        line-height: 18px;
    }

    .about-description-two__text--heading {
        font-size: 22px;
        line-height: 31px;
    }

    .container-one {
        margin-top: -135px;
    }

    .container-two {
        display: none;
    }

    .about-description-two__citata {
        font-size: 43px;
        line-height: 43px;
        margin-top: 200px;
    }
}

@media (max-width: 1224px) {
    .about-description {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .about-description__heading {
        align-self: flex-start;
        margin-bottom: 30px;
    }
}

@media (max-width: 1024px) {
    .about-description__heading {
        font-size: 60px;
        line-height: 61px;
    }

    .about-description-container {
        max-width: 500px;
    }

    .about-description-container__image {
        width: 683px;
        height: 341px;
    }

    .about-description-container__text {
        font-size: 13px;
        line-height: 18px;
    }
}

@media (max-width: 895px) {
    .about-description-container {
        overflow: hidden;
    }

    .about-description-container__contain {
        max-width: 420px;
    }
}

@media (max-width: 640px) {
    .about-description-container {
        max-width: 300px;
    }

    .about-description-container__image {
        position: relative;
        right: 45px;
    }
    
    .about-description-two__image-container {
        overflow: hidden;
    }
}

@media (max-width: 400px) {
    .about-description-container {
        max-width: 372px;
    }
}