.wrap--profilePage {
    padding-top: 150px;
    padding-bottom: 130px;
} 

.profilePage {
    background-color: var(--color-space);
    font-family: 'GilroyRegular';
    text-transform: uppercase;
    color: var(--color-korich);
} 

.profilePage__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.profilePage__title {
    margin: 0;
    color: var(--color-green);
    font-family: 'SantaCatarina';
    font-weight: 400;
    font-size: 90px;
    line-height: 91px;
    padding-bottom: 43px;
    position: relative;
    z-index: 1;
}

.profilePage__container-image {
    width: 0;
    height: 0;
}

.profilePage__image {
    width: 389px;
    height: 388px;
    position: relative;
    z-index: 0;
    right: 235px;
    bottom: 205px;
}

.profilePage__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    padding-bottom: 30px;
}

.profilePage__content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 35px;
    position: relative;
    z-index: 1;
}

.profilePage__profile-image {
    width: 64px;
    height: 64px;
    position: relative;
    bottom: 15px;
}

.profilePage__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 15px;
    max-width: 350px;
}

.profilePage__input-group {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    gap: 35px;
    width: 100%;
    padding-bottom: 5px;
    border-bottom: 1px solid var(--color-green);
}

.profilePage__input-label {
    min-width: 95px;
    font-weight: 500;
    font-size: 15px;
    line-height: 21px;
}

.profilePage__input {
    font-family: 'GilroyRegular';
    width: 100%;
    padding: 0;
    margin: 0;
    border: none;
    background-color: transparent;
    color: var(--color-korich);
    min-width: 95px;
    font-weight: 500;
    font-size: 15px;
    line-height: 21px;
}

.profilePage__input::placeholder {
    font-family: 'GilroyRegular';
    color: var(--color-korich);
    min-width: 95px;
    font-weight: 500;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
    text-transform: uppercase;
}

.profilePage__input:disabled {
    font-family: 'GilroyRegular';
    color: var(--color-korich);
    min-width: 95px;
    font-weight: 500;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
    text-transform: uppercase;
}

.profilePage__button {
    display: flex;
    align-self: center;
    background-color: var(--color-space);
    border: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    text-transform: uppercase;
    color: var(--color-korich);
    padding: 70px 60px 71px 60px;
}

.profilePage__button:hover {
    background-image: url(../../assets/buttonRegistrationLine.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

.profilePage__button:active {
    background-color: var(--color-space);
}

.programs__section-title--profile {
    font-family: 'GilroyRegular';
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    padding-bottom: 40px;
}


@media (max-width: 1522px) {

}

@media (max-width: 1024px) {
    .wrap--profilePage {
        padding-top: 100px;
    }

    .profilePage__title {
        font-size: 60px;
        line-height: 61px;
    }

    .profilePage__container-image {
        display: none;
    }

    .profilePage__subtitle {
        font-size: 25px;
        line-height: 35px;
    }

    .profilePage__input-label {
        font-size: 13px;
        line-height: 18px;
    }

    .profilePage__input {
        font-size: 13px;
        line-height: 18px;
    }
}

