.myModal {
    position: absolute;
    z-index: 4;
    top: 3295px;;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background-color: rgba(0, 0, 0, 0.5);
}

.myModalContent {
    min-width: 100%;
    box-shadow: 27px 27px 54px rgba(0, 0, 0, 0.3);
    opacity: 0;
    animation: ani 1s forwards;
}

.myModal.active {
    overflow: visible;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes ani {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@media (max-width: 1522px) {
    .myModal {
        top: 3010px;
    }
}

@media (max-width: 1024px) {
    .myModal {
        top: 3720px;
    }
}

@media (max-width: 640px) {
    .myModal {
        top: 3140px;
    }
}

@media (max-width: 400px) {
    .myModal {
        top: 3150px;
    }
}