.headerMain {
    background-color: var(--color-bej);
}


.headerMain-container {
    padding-top: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.headerMain-container__left {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.headerMain-container__content {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.headerMain-container__images {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.headerMain-container__image {
    position: relative;
    z-index: 1;
}

.headerMain-container__title {
    position: relative;
    z-index: 2;
    font-family: 'SantaCatarina';
    font-weight: 400;
    font-size: 90px;
    line-height: 90.81px;
    color: var(--color-green);
    white-space: nowrap;
    margin: 0;
    top: -63px;
    left: -64px;
}


.program__a {
    text-decoration: none;
    color: #5A4041;

}

.headerMain-container__button {
    display: flex;
    align-self: flex-end;
    position: relative;
    bottom: 139px;
    right: 215px;
    width: 0px;
}

.headerMain-container__right {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    max-height: 500px;
}


.header-container__image--left-line {
    position: relative;
    z-index: 0;
    bottom: 59px;
    right: 129px;
}

.header-container__image--right-image {
    position: relative;
    right: 70px;
    bottom: 431px;
}

.header-container__image--right-line {
    z-index: 0;
    position: relative;
    left: 86px;
    bottom: 187px;
}

.headerMain__button {
    font-family: 'GilroyRegular';
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    text-transform: uppercase;
    color: var(--color-korich);
    border: none;
    background-color: var(--color-bej);
    cursor: pointer;
    white-space: nowrap;
    padding: 59px 25px 58px 26px;
}

.headerMain__button:hover {
    background-image: url(../../assets/headerMainLine.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

@media (max-width: 1522px) {
    .headerMain-container__right {
        display: none;
    }
}

@media (max-width: 1024px) {
    .headerMain-container__content {
        flex-direction: column-reverse;
        align-items: flex-start;
    }

    .headerMain-container__images {
        position: relative;
        bottom: 84px;
    }

    .headerMain-container__left {
        flex-direction: column;
    }

    .headerMain-container__title  {
        top: 0;
        left: 0;
    }
    
    .headerMain-container__button {
        align-self: center;
        position: relative;
        right: 0;
        bottom: 100px;
        width: auto;
    }

    .header-container__image--left-line {
        z-index: 2;
        bottom: 103px;
        right: 129px;
    }
}

@media (max-width: 640px) {
    .headerMain-container__title {
        font-size: 60px;
        line-height: 61px;
    }
    
    .wrap {
        padding-left: 3px;
        padding-right: 3px;
    }

    .header-container__image--left-girl {
        width: 280px;
        height: 382px;
    }

    .headerMain-container__images {
        position: relative;
        bottom: 65px;
    }

    .headerMain-container__button {
        align-self: center;
        position: relative;
        right: 0;
        bottom: 128px;
        width: auto;
    }
}