.myModal {
    display: none;
    position: relative;
    top: 80px;
    right: 5px;
    margin-top: -70px;
}

.myModalContent {
    padding: 20px 11px 14px 11px;
    background: var(--color-green);
    width: 180px;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 1024px) {
    .myModal {
        position: fixed;
        width: 100%;
        top: 70px;
        right: 0;

    }

    .myModalContent {
        min-width: 100%;
    }
}

