.wrap--education {
    padding-top: 150px;
    padding-bottom: 150px;
}

.ed__main{
    background-color: var(--color-space);
}

.education {
    font-family: 'GilroyRegular';
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}


.education__title {
    margin: 0;
    font-family: 'SantaCatarina';
    color: var(--color-green);
    font-weight: 400;
    font-size: 90px;
    line-height: 91px;
    text-transform: uppercase;
}

.education__content {
    display: flex;
    padding-top: 30px;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    gap: 95px;
}

.education__content-heading {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
}

.education__subcontent {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 380px;
    gap: 12px;
}

.education__text {
    margin: 0;
    color: var(--color-green);
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
    max-width: 380px;
} 

.education__text--big {
    font-size: 30px;
    line-height: 42px;
}

.education__images {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.education__image {
}

.education__image--stand-girl {
    position: relative;
}

.education__image--lies {
    position: relative;
    right: 187px;
}

.education__certificates {
    padding-top: 150px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.education__certificate {
    margin-right: 90px;
}

.education__certificates-title {
    margin: 0;
    font-family: 'SantaCatarina';
    color: var(--color-korich);
    font-weight: 400;
    font-size: 90px;
    line-height: 91px;
    text-transform: uppercase;
    padding-bottom: 30px;
}

.education__carousel {
    max-width: 1170px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.education__carousel-arrow {
    position: relative;
    bottom: 55px;
    width: 25px;
    height: 25px;
    margin-right: 10px;
    cursor: pointer;
    padding: 10px;
}

.education__carousel-arrow--right {
    margin-right: 0;
    margin-left: 10px;
}

.education__certificate-text {
    margin: 0;
    padding-top: 40px;
    color: var(--color-korich);
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    text-transform: uppercase;
    max-width: 445px;
}



@media (max-width: 1200px) {
    .education__carousel {
        max-width: 1150px;
    }

    .education__certificate-image {
        width: 417px;
    }
    .styles-module_item-provider__YgMwz {
        width: 72%;
    }

}

@media (max-width: 1060px) {
    .education__carousel {
        max-width: 535px;
    }

    .education__certificate-image {
        width: 417px;
    }

    .education__certificates {
        margin:auto;
    }

}

@media (max-width: 1224px) {
    .education__title {
        font-size: 60px;
        line-height: 61px;
    }

    .education__text {
        font-size: 15px;
        line-height: 21px;
    }

    .education__text--big {
        font-size: 25px;
        line-height: 35px;
    }

    .education__content {
        flex-direction: column;
        gap: 66px;
    }

    .education__image--lies {
        right: 0;
        width: 503px;
    }

    .education__certificates {
        padding-top: 100px;
    }

    .education__certificates-title {
        font-size: 60px;
        line-height: 61px;  
    }

    .education__certificate-text {
        font-size: 25px;
        line-height: 35px;
    }
}

@media (max-width: 1024px) {

}

@media (max-width: 895px) {
    .education__image--lies {
        width: 446px;
    }

    .education__carousel {
        max-width: 405px;
    }

    .education__carousel {
        max-width: 360px;;
    }

    .education__certificate-image {
        width: 247px;
    }
}

@media (max-width: 640px) {
    .education__subcontent {
        overflow: hidden;
        max-width: 320px;
    }
}

@media (max-width: 400px) {
    .education__subcontent {
        max-width: 368px;
    }

    .education__certificate-text {
        max-width: 300px;
        padding-top: 30px;
    }
}