.wrap--program-modal {
    padding-top: 90px;
    padding-bottom: 90px;
    max-width: 100%;
    padding-left: 10.9375vw;
    padding-right: 10.9375vw;
}

.program-modal {
    font-family: 'GilroyRegular';
    background-color: var(--color-space);
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

.program-modal__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.program-modal__section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 85px;
    max-width: 308px;
    gap: 30px;
}

.program-modal__section:first-child {
    padding-top: 0;
}

.program-modal__title {
    margin: 0;
    color: var(--color-korich);
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    text-transform: uppercase;
    padding-bottom: 20px;
}

.program-modal__subtitle {
    margin: 0;
    color: var(--color-korich);
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

.program-modal__list-item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 31px;
    margin: 0;
    font-family: 'GilroyRegular';
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: var(--color-korich);
    max-width: 307px;
    text-transform: uppercase;
}

.program-modal__list-num {
    color: var(--color-green);
}

.program-modal__description {
    margin: 0;
    color: var(--color-korich);
    font-weight: 400;
    padding-bottom: 30px;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
    background-image: url(../../assets/programsUnderline.png);
    background-repeat: no-repeat;
    background-position: 0 62px;
    background-size: 100%;
}

.program-modal__title-two {
    margin: 0;
    color: var(--color-korich);
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

.program-modal__button {
    padding: 70px 54px 70px 55px;
    text-align: center;
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: var(--color-korich);
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    background-color: var(--color-space);
    align-self: center;
}

.program-modal__button:hover {
    background-image: url(../../assets/headerMainLine.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

.program-modal__images {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 635px;
}

.program-modal__image--line {
    position: relative;
    bottom: 65px;
    right: 233px;
    z-index: 5;
}

.button-exit {
    width: 80px;
    height: 80px;
    cursor: pointer;
    padding: 0;
    margin: 0;
    background-color: var(--color-space);
    border: none;
}

.button-exit--menu {
    align-self: flex-end;
    background-color: var(--color-green);
}

.button-exit__left {
    border: 2px solid var(--color-bej);
    transform: rotate(36.5deg);
    width: 73px;
    position: relative;
    top: 4px;
}

.button-exit__right {
    border: 2px solid var(--color-bej);
    transform: rotate(-36.5deg);
    width: 73px;
    position: relative;
}


@media (max-width: 1522px) {
    
    .wrap--program-modal {
        padding-top: 90px;
        padding-bottom: 90px;
        max-width: 100%;
    }

    .program-modal__images {
        display: none;
    }

    .program-modal__image {
        width: 520px;
    }

    .program-modal__image--line {
        width: 275px;
    }

}

@media (max-width: 1024px) {
    .program-modal {
        flex-direction: column;
        align-items: stretch;
    }
}

@media (max-width: 640px) {
    .wrap--program-modal {
        /* padding-left: 3px;
        padding-right: 3px; */
    }

}

@media (max-width: 400px) {
    .wrap--program-modal {
        padding-left: 3px;
        padding-right: 3px;
    }
  }