.header {
    width: auto;
    position: relative;
    top: 0px;
    z-index: 5;
    height: 80px;
    background-color: var(--color-bej);
    text-transform: uppercase;
}

.header__logo {
    width: 80px;
    height: 80px;
}

.wrap--header  {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.wrap-header--main {

}

.header__link {
    cursor: pointer;
}

.header__logo {

}

.navigation {

}

.header__navigation {
}

.navigation__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

.navigation__link {
    scroll-behavior: smooth;
    text-decoration: none;
    color: var(--color-korich);
    padding: 27px 13px 27px 13px;
    cursor: pointer;
    white-space: nowrap;
}

.navigation__link:hover {
    background-image: url(../../assets/headerLine.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

.navigation__item {
    font-family: 'GilroyRegular';
    font-weight: 400;
    font-size: 18px;
    line-height: 13.92px;
    text-transform: uppercase;
}

.header-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
}

.header-list__item {
    padding: 0;
    font-family: 'GilroyRegular';
    color: var(--color-space);
    cursor: pointer;
    font-weight: 400;
    font-size: 14px;    
    line-height: 13.92px;
}

.header-list--mobile {
    display: none;
}

.profile {
    width: 120px;
}

.profile-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}

.profile__button {
    position: relative;
    border: none;
    background-color: var(--color-bej);
    cursor: pointer;
}

.profile__logo {
    display: block;
}

.profile__logo--mobile-container {
    display: none;
    flex-direction: column;
    justify-content: space-between;
}

.profile__logo--mobile {
    display: block;
    width: 69px;
    border: 2px solid var(--color-green);
}


/* .header-list--mobile {
    width: 100%;
} */


@media (max-width: 1024px) {
    .header {
        height: 98px;
    }

    .header-list {
        display: none;
    }

    .header-list--mobile {
        display: flex;
    }

    .header-list {
        gap: 19px;
    }

    .header-list__item {
        font-size: 21px;
        line-height: 24px;
        color: var(--color-bej);
    }

    .header-list:last-child {
        padding-bottom: 58px;
    }
    
    .profile {
        width: 77px;
    }
    
    .profile__button {
        padding: 0;
    }

    .header__logo {
        width: 77px ;
    }

    .profile__logo {
        display: none;
    }

    .profile__logo--mobile-container {
        display: flex;
    }

    .navigation__list {
        display: none;
    }

}
