.wrap-auth {
    max-width: 352px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-family: 'GilroyRegular';
    width: 100%;
}

.auth {
    padding-top: 25px;
    padding-bottom: 75px;
    background-color: var(--color-bej);
    display: flex;
    justify-content: center;
}

.auth__title {
    margin: 0;
    font-family: 'santaCatarina';
    color: var(--color-green);
    font-weight: 400;
    font-size: 90px;
    line-height: 91px;
    text-transform: uppercase;
    padding-bottom: 50px;
}

.auth__input {
    width: 100%;
    padding: 0;
    margin: 0;
    border: none;
    background-color: var(--color-bej);
    border-bottom: 1px solid var(--color-green);
    padding-bottom: 5px;
    margin-bottom: 15px;
    color: var(--color-korich);
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

.auth__input::placeholder {
    color: var(--color-korich);
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

input:focus {
    outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.auth__checkbox-group {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
    padding-bottom: 20px;
}


.auth__checkbox {
    width: 15px;
    height: 15px;
    border: 1px solid var(--color-green);
    background-color: var(--color-bej);
}

.auth__checkbox-label {
    color: var(--color-korich);
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    text-transform: uppercase;
    margin-bottom: -2px;
    width: 300px;
}

.link--registration {
  text-transform: uppercase;
  border-bottom: 1px solid var(--color-green);
  padding-bottom: 3px;
  cursor: pointer;
  margin-top: 160px;
  color: var(--color-korich);
}

.auth__link {
    border-bottom: 1px solid var(--color-korich);
    width: 100%;
}


input[type=checkbox] {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
	border: 1px solid var(--color-green);
	background-color: var(--color-bej);
	cursor: pointer;
	line-height: 0;
	margin: 0 .6em 0 0;
	outline: 0;
	padding: 0 !important;
	height: 15px;
	width: 15px;
	-webkit-appearance: none;
}

input[type=checkbox]:hover {
}

input[type=checkbox]:checked {
  background-color: var(--color-green);
}

input[type=checkbox]:before {
}



.button {
  background-color: var(--color-bej);
  border: none;
  cursor: pointer;
}

.button:active {
  background-color: var(--color-bej);
}

.auth__bottom-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 130px;
  position: relative;
  right: 222px;
}

.auth__submit-button {
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-transform: uppercase;
  color: var(--color-korich);
  border: none;
  background-color: var(--color-bej);
  cursor: pointer;
  white-space: nowrap;
  padding: 59px 25px 58px 26px;
}

.auth__submit-button:hover {
  background-image: url(../../assets/buttonRegistrationLine.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.auth__submit-button--login:hover {
  background-image: url(../../assets/headerMainLine.png);
}


.link--bad-password {
  display: flex;
  align-self: flex-start;
  margin-top: -10px;
  font-size: 12px;
  border-bottom: 1px solid var(--color-green);
  padding-bottom: 2px;
  text-transform: uppercase;
  cursor: pointer;
}



