.footer {
    background-color: var(--color-bej);
    height: 220px;
}

.wrap--footer  {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.footer__link {

}

.footer__logo {

}

.footer__navigation {

}

.social {
    visibility: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;
}

.social.active {
    visibility: visible;
}

.footer__social {
    width: 120px;
    height: 25px;
}

@media (max-width: 1024px) {
    .footer__logo {
        width: 77px;
        height: 77px;
    }

    .wrap--footer {
        padding-top: 60px;
        padding-bottom: 30px;
        flex-direction: column;
    }

    .footer__social {
        margin-left: 0;
    }

    .social {
        justify-content: center;
    }

    .footer__navigation {
        display: flex;
        gap: 10px;
    }

    .navigation__item {
        font-size: 15px;
        line-height: 10px;
    }

    .navigation__link {
        padding: 0;
    }

    .navigation__link:hover {
        background-image: none;
    }
    
}

@media (max-width: 640px) {
    .navigation__item {
        font-size: 12px;
    }
}
