.wrap--footerMain {
    padding-top: 80px;
}

.footerMain {
    background-color: var(--color-bej);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.footerMain__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: 'GilroyRegular';
}

.footerMain__title {
    margin: 0;
    font-family: 'SantaCatarina';
    color: var(--color-green);
    font-weight: 400;
    font-size: 90px;
    line-height: 90.81px;
}

.footerMain__list {
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
}

.footerMain__item {
    cursor: pointer;
}

.footerMain__link {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 38px;  
}

.footerMain__link:hover {
    transform: scale(1.1);
}

.footerMain__icon {
    width: 20px;
}

.footerMain__text {
    color: var(--color-korich);
    font-weight: 400;
    font-size: 25px;
    line-height: 37.5px;
    text-transform: uppercase;
}

.footerMain__images {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.footerMain__image--girl {
    position: relative;
    z-index: 1;
}

.footerMain__image--line {
    margin-left: -215px;
    z-index: 0;
}


@media (max-width: 1500px) {
    .footerMain__images {
        display: none;
    }
}

@media (max-width: 1224px) {
    .footerMain__title {
        font-size: 60px;
        line-height: 61px;
    }

    .footerMain__text {
        font-size: 25px;
        line-height: 38px;
    }
}
