@import './styles/about/about.css';
@import './styles/header/header.css';
@import './styles/headerMain/headerMain.css';
@import './styles/footer/footer.css';
@import './styles/footerMain/footerMain.css';
@import './styles/programs/programs.css';
@import './styles/education/education.css';
@import './styles/programModal/programModal.css';
@import './styles/profilePage/profilePage.css';
@import './styles/authPage/authPage.css';
@import './styles/slider/slider.css';
@font-face {
  font-family: 'SantaCatarina';
  src: local('SantaCatarina'),
    url('./fonts/santaCatarina/SantaCatarina.ttf') format('truetype');
}

@font-face {
  font-family: 'GilroyRegular';
  src: local('GilroyRegular'),
    url('./fonts/gilroyRegular/GilroyRegular.ttf') format('truetype');
}

html {
  scroll-behavior: smooth; /*  scroll-behavior  ,     */ 
  min-height: 100%;
  background-color: var(--color-bej);
}

*[id] {
	border-top: 180px solid transparent;
	margin-top: -180px;
	-moz-background-clip: padding;
	-webkit-background-clip: padding-box;
	background-clip: padding-box;
}

@media (max-width: 1024px) {
  *[id] {
    border-top: 90px solid transparent;
    margin-top: -90px;
  }
}

.header {
  opacity: 0;
  animation: ani 0.5s forwards;
}

.headerMain {
  opacity: 0;
  animation: ani 1.5s forwards;
}

.myModal.active {
  opacity: 0;
  animation: ani 2.5s forwards;
}

.programs {
  opacity: 0;
  animation: ani 2s forwards;
}

.education {
  opacity: 0;
  animation: ani 3.5s forwards;
}

.footerMain {
  opacity: 0;
  animation: ani 3.5s forwards;
}

.footer {
  opacity: 0;
  animation: ani 3.5s forwards;
}

@keyframes ani {
  0% {opacity: 0;}
  100% {opacity: 1;}
}


.link {
  text-decoration: none;
  color: var(--color-korich);
}

.link:active {
  color: var(--color-korich);
}

:root {
  --color-korich: #5A4041;
  --color-bej: #D9BB9F;
  --color-green: #177462;
  --color-space: #E6E6E6;
}

.wrap {
  max-width: 1280px;
padding: 0 20px;
margin: 0 auto;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@media (max-width: 400px) {
  .wrap {
      padding-left: 3px;
      padding-right: 3px;
  }
}
