.wrap--programs {
    padding-top: 48px;
    padding-bottom: 100px;
    font-family: 'GilroyRegular';
} 

.prog__main{
    background-color: var(--color-space);
}

.programs {
    background-color: var(--color-space);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
}

.programs__intro {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center
}

.programs__title {
    color: var(--color-green);
    text-transform: uppercase;
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    margin: 0;
    margin-bottom: 186px;
}

.programs__images {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.programs__image--girl-seed {
    margin-left: -117px;
    margin-bottom: -40px;
}

.programs__approach {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 94px;
    padding-bottom: 150px;
}

.programs__text {
    margin: 0;
    color: var(--color-korich);
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    text-transform: uppercase;
}

.programs__highlight {
    color: var(--color-korich);
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    text-transform: uppercase;
}

.programs__creator {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.programs__creator-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: -74px;
}

.programs__creator-title {
    margin: 0;
    font-family: 'SantaCatarina';
    color: var(--color-green);
    font-weight: 400;
    font-size: 103px;
    line-height: 103px;
}

.programs__image--heart {
    position: relative;
    bottom: 109px;
    right: 9px;
}

.programs__section {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between
}

.programs__section-title {
    margin: 0;
    font-family: 'SantaCatarina';
    color: var(--color-korich);
    font-weight: 400;
    font-size: 90px;
    line-height: 91px;
    text-transform: uppercase;
}

.programs__program {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
}

.programs__program-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.programs__program-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding-left: 10px;
    padding-bottom: 30px;
    min-height: 508px;
}

.programs__program-title {
    margin: 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    color: var(--color-space);
    text-transform: uppercase;
    padding-bottom: 15px;
}

.programs__program-button {
    padding: 70px 54px 70px 55px;
    text-align: center;
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: var(--color-korich);
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    background-color: var(--color-space);
    background-image: url(../../assets/headerMainLine.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

.programs__program-button:hover {

}

.programs__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    text-decoration: none;
    cursor: auto; 
}

.programs__link-text:hover {
    background-image: url(../../assets/programsUnderline.png);
    background-repeat: no-repeat;
    background-position: 0 34px;
    background-size: 100%;
}

.programs__link-text {
    width: 312px;
    height: 62px;
    text-align: center;
    margin: 0;
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    color: var(--color-korich);
    cursor: pointer;
    text-transform: uppercase;
}


@media (max-width: 1522px) {
    .programs__image--girl-stand {
        width: 292px;
    }

    .programs__image--girl-seed {
        width: 162px;
    }

    .programs__creator-image {
        width: 506px;
    }

    .programs__image--heart {
        bottom: 168px;
    }
}

@media (max-width: 1024px) {
    .programs__title {
        margin-bottom: 0;
    }
    
    .programs__intro {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .programs__images {
        align-self: flex-end;
    }

    .programs__approach {
        flex-direction: column-reverse;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 20px;
        padding-top: 175px;
    }

    .programs__creator-container {
        flex-direction: row-reverse;
        margin-left: 0;
    }

    .programs__creator-title {
        max-width: 368px;
        font-size: 60px;
        line-height: 61px;
    }
    
    .programs__image--heart {
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        bottom: 165px;
        right: 130px;
        bottom: 168px;
        right: 9px;
    }

    .programs__program {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }



}

@media (max-width: 640px) {
    .programs__title {
        font-size: 13px;
        line-height: 18px;
    }

    .programs__image--girl-stand {
        width: 174px;
    }

    .programs__image--girl-seed {
        width: 120px;
    }

    .programs__image--heart {
        bottom: 110px;
    }

    .programs__creator-image {
        width: 368px;
    }

    .programs__text {
        font-size: 13px;
        line-height: 18px;
    }

    .programs__highlight {
        font-size: 25px;
        line-height: 35px;
    }

    .programs__section-title {
        font-size: 60px;
        line-height: 61px;
        padding-bottom: 30px;
    }

    .programs__program-title {
        padding-bottom: 8px;
    }

    .programs__program-image {
        width: 362px;
    }

}